import React, { useState, useEffect } from 'react'
import { GrSearch } from 'react-icons/gr';
import { useFormikContext } from 'formik'
import debounce from 'lodash/debounce'

import Input from '../components/Input';
import Button from '../components/Button';

const Search = ({ isMobile, ...props }) => {
  const [search, handleSearch] = useState()
  const { values, setFieldValue } = useFormikContext()
  const [value, setValue] = useState(values.search)
  const isFilter = values.location.concat(values.journal).length > 0

  const sendValueToFormik = debounce(() => {
    setFieldValue('search', value)
  }, 750)

  useEffect(() => {
    sendValueToFormik()
  }, [value])
  return isFilter || search ? (
    <Input.Search
      name="search"
      autoFocus
      value={value}
      isFilter={isFilter}
      onBlur={() => handleSearch(value ? true : false)}
      onChange={(e) => setValue(e.target.value)}
      placeholder="輸入關鍵字"
      {...props}
    />
  ) : (
    <Button.Transparent
      mx="0.5em"
      leftIcon={GrSearch}
      onClick={() => handleSearch(!search)}
      mt={isMobile && '0.25em'}
    >
      {!isMobile && '搜尋'}
    </Button.Transparent>
  )
}

export default Search
