import React from 'react'
import isArray from 'lodash/isArray'
import range from 'lodash/range'

import WhiteBox from './WhiteBox'

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Circle from '../../components/Circle'
import Title from '../../components/TitleWithBg'
import Image from '../../components/Image'
// import { responsive } from '../../components/ThemeProvider/theme';
import useResponsive from '../../contexts/mediaQuery/useResponsive'

const height = {
  blue: '3000m以上',
  pink: '2000-3000m',
  orange: '1000-2000m',
  green: '500-1000m',
  gray: '0-500m',
}

const ModuleAnimal = ({
    animals,
    img,
    title,
    length,
    index,
    animalName,
    color,
    active,
    text,
    ...props
  }) => {
  const { isMobile } = useResponsive()
  const src = isArray(img) ? img[Number(isMobile)] : img
  return (
    <WhiteBox fontWeight="bold" color={color} ml={(!isMobile && index) &&'0.75em'} p="0" mt="1em" {...props}>
      {img ? (
        <Box position="relative">
          <Image src={src} />
          <Box
            position="absolute"
            top={(isMobile || length > 2) ? '1em' : '50%'}
            width={(isMobile || length > 2) ? '100%' : '60%'}
            textAlign="center"
            transform={!(isMobile || length > 2) && 'translateY(-50%)'}
          >
            <Title bg={color}>{title}</Title>
            <Box mt="0.5em" textAlign="center">
              {animals.map(({ Icon, name }, k) => (
                <Flex alignItems="center" key={k}>
                  <Box width={(isMobile || length > 2) ? '25%' : '20%'} textAlign="right">●</Box>
                  <Flex m="0.25em">
                    <Box mr="0.5em" fontSize="1.125em">{name}</Box>
                    {isArray(Icon) ?
                      Icon.map((I, i) => (
                        <I width="1.5em" key={i} />
                      ))
                      : <Icon width="1.5em" />
                    }
                  </Flex>
                </Flex>
              ))}
            </Box>
          </Box>
          <Box
            position="absolute"
            left={(isMobile || length > 2) && '1em'}
            right={!(isMobile || length > 2) && '1em'}
            textAlign={(isMobile || length > 2) ? 'auto' : 'right'}
            bottom="0.5em"
          >
            <Title bg={color} fontSize="0.875em">{animalName}</Title>
            <Text>{height[color]}山代表</Text>
          </Box>
        </Box>
      ) : (
        <Box textAlign="center" p="1em">
          <Title bg="white" border="1px solid" color={color}>季節性活動</Title>
          <Flex flexWrap="wrap" mt="3em" mx="1em" mb="2.625em">
            {range(12).map((dot) => (
              <Box width={1 / 6} px="0.25em" mt="1em">
                <Circle
                  bg={active.includes(dot + 1) ? color : 'white'}
                  color={active.includes(dot + 1) ? 'white' : color}
                  border="1px solid"
                  key={dot}
                >{dot + 1}</Circle>
              </Box>
            ))}
          </Flex>
          <Text textAlign="left">{text}</Text>
        </Box>
      )}
    </WhiteBox>
  )
}

export default ModuleAnimal
